/* Web3 DApp Styles */
:root {
  --primary: #6851ff;
  --primary-dark: #5843d4;
  --secondary: #46c7c7;
  --secondary-dark: #3aa3a3;
  --bg-dark: #0f1023;
  --bg-card: #1a1b31;
  --text: #ffffff;
  --text-secondary: #a0a0b8;
  --success: #05c581;
  --warning: #ffa600;
  --error: #ff5353;
  --border: rgba(255, 255, 255, 0.1);
  --card-gradient: linear-gradient(135deg, rgba(72, 59, 175, 0.25) 0%, rgba(45, 34, 112, 0.15) 100%);
  --btn-gradient: linear-gradient(90deg, var(--primary) 0%, var(--secondary) 100%);
  --box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter', 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body {
  background-color: var(--bg-dark);
  color: var(--text);
  line-height: 1.6;
}

.app-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Header Styles */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 0;
  position: relative;
  z-index: 10;
  border-bottom: 1px solid var(--border);
  margin-bottom: 20px;
}

.logo {
  display: flex;
  align-items: center;
  gap: 10px;
}

.logo-text {
  font-size: 24px;
  font-weight: 700;
  background: var(--btn-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -0.5px;
}

.round-badge {
  background: rgba(104, 81, 255, 0.15);
  color: var(--primary);
  padding: 6px 10px;
  border-radius: 12px;
  font-size: 14px;
  font-weight: 600;
  margin-left: 12px;
}

.connect-button {
  background: var(--btn-gradient);
  border: none;
  color: white;
  padding: 12px 24px;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  box-shadow: var(--box-shadow);
}

.connect-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

.connect-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.connect-button.loading {
  position: relative;
  color: transparent;
}

.connect-button.loading::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  margin: -10px 0 0 -10px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top-color: white;
  border-radius: 50%;
  animation: spinner 0.6s linear infinite;
}

.account-info {
  display: flex;
  align-items: center;
  gap: 12px;
}

.account-address {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  padding: 8px 16px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.address-label {
  color: var(--text-secondary);
  font-size: 14px;
}

.address-value {
  font-weight: 600;
  font-family: 'Roboto Mono', monospace;
  letter-spacing: 0.5px;
}

.owner-badge {
  background: rgba(255, 166, 0, 0.15);
  color: var(--warning);
  padding: 6px 10px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 600;
}

/* Dashboard Styles */
.dashboard {
  flex: 1;
  position: relative;
}

.lottery-status {
  margin-bottom: 30px;
  position: relative;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.status-indicator {
  font-size: 18px;
  font-weight: 700;
  padding: 10px 20px;
  border-radius: 30px;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  display: inline-block;
}

.status-indicator.open {
  background: rgba(5, 197, 129, 0.15);
  color: var(--success);
}

.status-indicator.closed {
  background: rgba(255, 83, 83, 0.15);
  color: var(--error);
}

.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(15, 16, 35, 0.8);
  backdrop-filter: blur(2px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
  border-radius: 8px;
}

.loader {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(255, 255, 255, 0.1);
  border-top-color: var(--primary);
  border-radius: 50%;
  animation: spinner 0.8s linear infinite;
}

@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

.main-stats {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.stat-card {
  background: var(--card-gradient);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 24px;
  box-shadow: var(--box-shadow);
  border: 1px solid var(--border);
  text-align: center;
  position: relative;
  overflow: hidden;
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--btn-gradient);
}

.stat-card h3 {
  font-size: 16px;
  font-weight: 400;
  color: var(--text-secondary);
  margin-bottom: 10px;
}

.stat-value {
  font-size: 28px;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
}

.eth-symbol {
  color: var(--secondary);
  font-size: 16px;
  font-weight: 500;
}

.winner-section {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 16px;
  padding: 20px;
  text-align: center;
  margin-bottom: 30px;
  border: 1px solid var(--border);
}

.winner-title {
  color: var(--text-secondary);
  font-size: 16px;
  margin-bottom: 10px;
}

.winner-address {
  font-size: 18px;
  font-weight: 700;
  font-family: 'Roboto Mono', monospace;
  background: linear-gradient(90deg, var(--warning) 0%, var(--secondary) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Buy Ticket Section */
.buy-ticket-section {
  margin-bottom: 30px;
}

.ticket-card {
  background: linear-gradient(135deg, rgba(5, 197, 129, 0.1) 0%, rgba(104, 81, 255, 0.1) 100%);
  border-radius: 16px;
  padding: 24px;
  box-shadow: var(--box-shadow);
  border: 1px solid rgba(5, 197, 129, 0.3);
  text-align: center;
  position: relative;
  overflow: hidden;
}

.ticket-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, var(--success) 0%, var(--primary) 100%);
}

.ticket-card h3 {
  font-size: 20px;
  font-weight: 700;
  color: var(--text);
  margin-bottom: 12px;
}

.ticket-desc {
  color: var(--text-secondary);
  font-size: 14px;
  margin-bottom: 20px;
}

.ticket-input-container {
  display: flex;
  align-items: center;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 4px 12px;
  margin: 0 auto 20px;
  max-width: 300px;
  position: relative;
}

.ticket-input {
  background: transparent;
  border: none;
  color: var(--text);
  font-size: 18px;
  font-weight: 600;
  padding: 12px 8px;
  width: 100%;
  text-align: center;
}

.ticket-input:focus {
  outline: none;
}

.ticket-input::-webkit-inner-spin-button,
.ticket-input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.eth-label {
  color: var(--secondary);
  font-size: 16px;
  font-weight: 600;
  padding-left: 8px;
}

.buy-button {
  background: linear-gradient(90deg, var(--success) 0%, var(--primary) 100%);
  border: none;
  color: white;
  padding: 14px 32px;
  border-radius: 12px;
  font-weight: 600;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s;
  margin-bottom: 16px;
  box-shadow: 0 8px 16px rgba(5, 197, 129, 0.25);
}

.buy-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 20px rgba(5, 197, 129, 0.35);
}

.buy-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.ticket-message {
  padding: 12px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.05);
  margin-top: 16px;
  font-size: 14px;
  font-weight: 500;
}

.ticket-message.success {
  background: rgba(5, 197, 129, 0.1);
  color: var(--success);
  border: 1px solid rgba(5, 197, 129, 0.3);
}

.ticket-message.error {
  background: rgba(255, 83, 83, 0.1);
  color: var(--error);
  border: 1px solid rgba(255, 83, 83, 0.3);
}

/* Action Cards */
.action-section {
  margin-bottom: 40px;
}

.action-section h2 {
  margin-bottom: 20px;
  font-size: 24px;
  color: var(--text);
  position: relative;
  padding-left: 15px;
}

.action-section h2::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  width: 5px;
  height: 20px;
  background: var(--primary);
  transform: translateY(-50%);
  border-radius: 4px;
}

.action-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.action-card {
  background: var(--bg-card);
  border-radius: 16px;
  padding: 24px;
  box-shadow: var(--box-shadow);
  border: 1px solid var(--border);
}

.action-card h3 {
  margin-bottom: 20px;
  font-size: 18px;
  color: var(--text);
}

.action-button {
  background: rgba(104, 81, 255, 0.1);
  color: var(--primary);
  border: 1px solid rgba(104, 81, 255, 0.3);
  border-radius: 8px;
  padding: 10px 16px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  width: 100%;
}

.action-button:hover {
  background: rgba(104, 81, 255, 0.15);
  border-color: var(--primary);
}

.action-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.player-list {
  margin-top: 20px;
  max-height: 200px;
  overflow-y: auto;
  padding-right: 10px;
}

.player-list ul {
  list-style: none;
}

.player-list li {
  padding: 10px;
  margin-bottom: 8px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  font-family: 'Roboto Mono', monospace;
  font-size: 14px;
}

.player-list p {
  color: var(--text-secondary);
  text-align: center;
  font-style: italic;
}

/* Owner Actions */
.owner-actions {
  background: linear-gradient(135deg, rgba(72, 59, 175, 0.1) 0%, rgba(255, 166, 0, 0.05) 100%);
}

.owner-actions h3 {
  color: var(--warning);
}

.action-row {
  margin-bottom: 16px;
  position: relative;
}

.action-row .message {
  margin-top: 8px;
  font-size: 13px;
  padding: 6px 10px;
  border-radius: 4px;
  background: rgba(255, 255, 255, 0.05);
}

.platform-address {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.platform-address input {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--border);
  color: var(--text);
  padding: 12px;
  border-radius: 8px;
  width: 100%;
}

.platform-address input:focus {
  outline: none;
  border-color: var(--primary);
}

/* Welcome Section */
.welcome-section {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px 0;
}

.welcome-content {
  text-align: center;
  max-width: 600px;
}

.welcome-content h1 {
  font-size: 40px;
  font-weight: 800;
  margin-bottom: 16px;
  background: linear-gradient(90deg, var(--primary) 0%, var(--secondary) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.welcome-content p {
  font-size: 18px;
  color: var(--text-secondary);
  margin-bottom: 40px;
}

.welcome-features {
  display: flex;
  justify-content: center;
  gap: 40px;
  margin-top: 40px;
}

.feature {
  text-align: center;
}

.feature-icon {
  font-size: 32px;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70px;
  height: 70px;
  margin: 0 auto 16px;
  border-radius: 20px;
  background: var(--card-gradient);
  border: 1px solid var(--border);
}

.feature-text {
  font-weight: 600;
  color: var(--text-secondary);
}

/* Footer */
.footer {
  padding: 20px 0;
  text-align: center;
  border-top: 1px solid var(--border);
  margin-top: 20px;
}

.footer p {
  color: var(--text-secondary);
  font-size: 14px;
}

/* Scrollbar */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: var(--primary-dark);
  border-radius: 10px;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .header {
    flex-direction: column;
    gap: 20px;
    text-align: center;
    padding: 16px 0;
  }
  
  .logo {
    justify-content: center;
  }
  
  .connect-button {
    width: 100%;
  }
  
  .account-info {
    flex-direction: column;
    width: 100%;
  }
  
  .account-address {
    width: 100%;
    justify-content: center;
  }
  
  .main-stats {
    grid-template-columns: 1fr;
  }
  
  .action-cards {
    grid-template-columns: 1fr;
  }
  
  .welcome-features {
    flex-direction: column;
    gap: 30px;
  }
  
  .welcome-content h1 {
    font-size: 32px;
  }
  
  .buy-button {
    width: 100%;
  }
} 